    @import url('https://fonts.googleapis.com/css2?family=Berkshire+Swash&family=Poppins:wght@200;500&display=swap');
    @import url('https://fonts.googleapis.com/css2?family=Maven+Pro:wght@400;500;600&display=swap');
    * {
        margin: 0%;
        padding: 0%;
    }
    
    body {
        margin: 0;
        font-family: 'Maven Pro', sans-serif;
        /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif; */
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
    }
    
    code {
        font-family: 'Maven Pro', sans-serif;
        /* font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace; */
    }
/*     
    html {
        font-size: 80% !important;
    } */
    /* // utility alert */
    
    .alert {
        font-size: 1.2rem !important;
        font-weight: 400 !important;
    }
    
    .snackbar div {
        display: flex !important;
        align-items: center !important;
    }
    
    .Banner {
        justify-content: center;
        align-items: center;
        background-image: linear-gradient(rgba(250, 243, 230, 0.644), rgba(250, 243, 230, 0.678)), url('/src/asset/images//banner.png');
        background-size: cover;
        height: fit-content;
        background-position: center;
        background-repeat: no-repeat;
        padding: 7% 0% !important;
        text-align: center;
        font-weight: 500 !important;
        color: #88250E;
        font-family: 'Poppins', sans-serif;
    }
    
    .fallback {
        display: flex;
        width: 50%;
        justify-content: center;
        text-align: center;
        flex-direction: column;
        margin: auto;
        margin-top: 20%;
    }
    
 
    
    @media screen and (width >= 1900px) {
        html {
            font-size: 100% !important;
        }
    }
    
    @media screen and (width <= 1400px) {
        html {
            font-size: 85% !important;
        }
    }
    
    @media screen and (width <= 800px) {
        html {
            font-size: 80% !important;
        }
    }

    @media screen and (width <= 500px ) {
        .alert {
            font-size: 1.5rem !important;
        }
        html {
            font-size: x-small !important;
        }
    }